<script>

export default {
  data() {
    return {
      invoiceData: {},
      studentInfo: {
        fullName: "",
        regId: "",
        className: "",
        rollNumber: "",
        parentName: "",
        parentPhoneNumber: ""
      },
      admissionFees: {
        admissionFee: "",
        admissionFormFee: "",
        idCardFee: ""
      },
      examFees: {
        examFee: "",
        missedExamFee: ""
      },
      tutionFees: {
        monthlyTutionFee: "",
        lateTutionFee: "",
        lateEntryFee: "",
        missedClassFee: "",
        previousDues: ""
      },
      otherFees: {
        annualSportsFee: "",
        annualMahfilFee: "",
        annualPicnicFee: ""
      },
      isPreview: false,
      isModalActive: false,
      isModalDeactive: false,
      printReady: false,
      isInvoiceHidden: true
    };
  },
  computed: {
    admissionFee() {
      return (
        Number(this.admissionFees.admissionFee) +
        Number(this.admissionFees.admissionFormFee) +
        Number(this.admissionFees.idCardFee)
      );
    },
    tutionFee() {
      return (
        Number(this.tutionFees.monthlyTutionFee) +
        Number(this.tutionFees.lateTutionFee) +
        Number(this.tutionFees.lateEntryFee) +
        Number(this.tutionFees.missedClassFee)
      );
    },
    examFee() {
      return (
        Number(this.examFees.examFee) + Number(this.examFees.missedExamFee)
      );
    },
    otherFee() {
      return (
        Number(this.otherFees.annualPicnicFee) +
        Number(this.otherFees.annualSportsFee) +
        Number(this.otherFees.annualMahfilFee)
      );
    },
    invoiceDate() {
      let today = new Date();
      let dd = today.getDate();

      let mm = today.getMonth() + 1;
      const yyyy = today.getFullYear();
      if (dd < 10) {
        dd = `0${dd}`;
      }

      if (mm < 10) {
        mm = `0${mm}`;
      }
      today = `${dd}-${mm}-${yyyy}`;
      return today;
    },
    invoiceNum() {
      return Math.floor(100000 + Math.random() * 900000);
    },
    totalFee() {
      return (
        Number(this.admissionFee) +
        Number(this.tutionFee) +
        Number(this.examFee) +
        Number(this.otherFee)
      );
    }
  },
  methods: {
    previewInvoice(){
      let invoiceData = {};
      invoiceData.studentInfo = this.studentInfo;
      invoiceData.admissionFees = this.admissionFees;
      invoiceData.examFees = this.examFees;
      invoiceData.tutionFees = this.tutionFees;
      invoiceData.otherFees = this.otherFees;
      invoiceData.totalFee = this.totalFee;
      invoiceData.invoiceNum = this.invoiceNum;
      invoiceData.invoiceDate = this.invoiceDate;
      localStorage.setItem('invoiceData', JSON.stringify(invoiceData))
      this.$router.push('invoice')
    },
    resetFields(){
      this.studentInfo = {
        fullName: "",
        regId: "",
        className: "",
        rollNumber: "",
        parentName: "",
        parentPhoneNumber: ""
      };
      this.admissionFees =  {
        admissionFee: "",
        admissionFormFee: "",
        idCardFee: ""
      },
      this.examFees = {
        examFee: "",
        missedExamFee: ""
      };
      this.tutionFees =  {
        monthlyTutionFee: "",
        lateTutionFee: "",
        lateEntryFee: "",
        missedClassFee: "",
        previousDues: ""
      },
      this.otherFees = {
        annualSportsFee: "",
        annualMahfilFee: "",
        annualPicnicFee: ""
      };
      localStorage.removeItem('invoiceData')
      
    },
    getDataFromLocalStoreage(){
      if(JSON.parse(localStorage.getItem("invoiceData")) != null){
         this.invoiceData = JSON.parse(localStorage.getItem("invoiceData"));
          this.studentInfo = this.invoiceData.studentInfo;
          this.admissionFees = this.invoiceData.admissionFees;
          this.examFees = this.invoiceData.examFees;
          this.tutionFees = this.invoiceData.tutionFees;
          this.otherFees = this.invoiceData.otherFees;
      }
    } 
    
  },
  created() {
    this.getDataFromLocalStoreage();
  }
};
</script>

