<script>
import html2pdf from "html2pdf.js";

export default {
  data() {
    return {
      invoiceData: {}
    };
  },
  computed: {},
  methods: {
    printInvoice() {
      window.print();
      //localStorage.removeItem('invoiceData')
      //this.$router.push('/')
    },
    async saveFileAsPdf() {
        let element = await document.getElementById("modal-content-print");
        let opt = {
            margin: 0.2,
            filename: `RegId-${this.invoiceData.studentInfo.regId}-${this.invoiceData.studentInfo.fullName}-roll-${this.invoiceData.studentInfo.rollNumber}.pdf`,
            image: { type: "jpeg", quality: 0.98 },
            pagebreak: { mode: ['avoid-all', 'css', 'legacy'] },
            jsPDF: { unit: "in", format: "letter", orientation: "portrait" }
        };
        //New Promise-based usage:
          html2pdf()
            .set(opt)
            .from(element)
            .save()

        localStorage.removeItem('invoiceData')
        this.$router.push('/')
    },
    getDataFromLocalStoreage(){
      this.invoiceData = JSON.parse(localStorage.getItem("invoiceData"));
    } 
  },
  created() {
    this.getDataFromLocalStoreage();
  }
};
</script>

